import React, { Component } from 'react';
import { connect } from 'react-redux';
import db from '../../PortfolioDB';
import { changeRoute } from '../../actions';
import Gallery from 'react-grid-gallery';
import './Project.css';

const mapStateToProps = state => {
    return {
        projectID: state.handleRoute.projectID
    }
}
  
const mapDispatchToProps = (dispatch) => {
    return {
        changeRoute: (text) => dispatch(changeRoute(text))
    }
}

class Project extends Component {
    render() {
        const { projectID, changeRoute } = this.props;

        return (
            <div className="section section-our-clients-freebie" id="project">
                <div className="container">
                    <div className="title-area">
                        <h2>{db[projectID].name}</h2>
                        <div className="separator separator-danger">∎</div>
                    </div>
                    <div className="tab-content">
                        <div className='f3'>
                            { db[projectID].blurb }
                        </div>
                        <div className='tech-list'>
                            { db[projectID].tech.join(" 。 ")}
                        </div>
                        <div className='screenshot-container'>
                            <Gallery images={db[projectID].images} enableImageSelection={false} backdropClosesModal={true}/>
                        </div>
                        <div className='f7 center'><a href='#portfolio' onClick={() => changeRoute('portfolio')}>&lt; back to project list</a></div>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Project);