import React, { Component } from 'react';
import { connect } from 'react-redux';
import db from '../../PortfolioDB';
import { setProjectID } from '../../actions';
import './Card.css';

const mapStateToProps = state => {
    return {

    }
}
  
const mapDispatchToProps = (dispatch) => {
    return {
        setProjectID: (id) => dispatch(setProjectID(id))
    }
}

class Card extends Component {
    render() {
        const { projectID, setProjectID } = this.props;

        return (
            <a href='#project' onClick={() => setProjectID(projectID)}>
            <div className='card'>
                <img className='cards' src={`${db[projectID].images[0].src}`} alt=''/>
                <div className='card-blurb'>
                    <p className='ttc f5 bg-white-70'>{db[projectID].blurb}</p>
                </div>
            </div>
            </a>
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Card);